import React, { useEffect, useRef, useState } from "react";
import { HubConnectionBuilder } from "@microsoft/signalr";

const App = () => {
  const [connection, setConnection] = useState(null);
  const [localStream, setLocalStream] = useState(null);
  const [remoteStream, setRemoteStream] = useState(null);
  const [roomId, setRoomId] = useState("");
  const [isConnected, setIsConnected] = useState(false);
  const [error, setError] = useState("");
  const [connectionStatus, setConnectionStatus] = useState("Disconnected");

  const localVideoRef = useRef();
  const remoteVideoRef = useRef();
  const peerConnection = useRef(null);

  const iceServers = {
    iceServers: [
      { urls: "stun:stun.l.google.com:19302" },
      { urls: "stun:stun1.l.google.com:19302" }
    ]
  };

  const initializeConnection = async () => {
    try {
      const conn = new HubConnectionBuilder()
        // .withUrl("https://localhost:5001/videocallhub")
        .withUrl("https://stupefied-jang.180-179-213-167.plesk.page/videocallhub")
        .withAutomaticReconnect()
        .build();

      conn.on("ReceiveSignal", async (senderId, signal) => {
        try {
          if (!peerConnection.current) {
            await initializePeerConnection();
          }
          
          if (signal.type === "offer") {
            await handleReceiveOffer(signal);
          } else if (signal.type === "answer") {
            const desc = new RTCSessionDescription(signal);
            await peerConnection.current.setRemoteDescription(desc);
          } else if (signal.candidate) {
            await peerConnection.current.addIceCandidate(new RTCIceCandidate(signal));
          }
        } catch (err) {
          console.error('Signal handling error:', err);
          setError(`Signal handling error: ${err.message}`);
        }
      });

      conn.on("UserJoined", async (userId) => {
        console.log('User joined:', userId);
        try {
          if (!peerConnection.current) {
            await initializePeerConnection();
          }
          await createOffer();
        } catch (err) {
          console.error('UserJoined error:', err);
          setError(`UserJoined error: ${err.message}`);
        }
      });

      conn.on("UserLeft", () => {
        cleanupVideoCall();
      });

      await conn.start();
      return conn;
    } catch (err) {
      console.error('Connection error:', err);
      setError(`Connection error: ${err.message}`);
      return null;
    }
  };

  useEffect(() => {
    let isActive = true;

    const initialize = async () => {
      try {
        await initLocalStream();
        
        if (isActive) {
          const conn = await initializeConnection();
          if (conn) {
            setConnection(conn);
            setIsConnected(true);
            setConnectionStatus("Connected");
          }
        }
      } catch (err) {
        if (isActive) {
          console.error('Initialization error:', err);
          setError(`Initialization error: ${err.message}`);
        }
      }
    };

    initialize();

    return () => {
      isActive = false;
      cleanupVideoCall();
      if (connection) {
        connection.stop();
      }
    };
  }, []);

  const createOffer = async () => { 
    try {
      console.log("Connection state:", connection?.state);
      console.log("peerConnection:", peerConnection.current);
  
      if (!connection || connection.state !== "Connected") {
        throw new Error("Connection not ready");
      }
  
      if (!peerConnection.current) {
        await initializePeerConnection();
      }
  
      const offer = await peerConnection.current.createOffer({
        offerToReceiveAudio: true,
        offerToReceiveVideo: true
      });
  
      await peerConnection.current.setLocalDescription(offer);
      await connection.invoke("SendSignal", roomId, offer);
    } catch (err) {
      console.error('Offer creation error:', err);
      setError(`Offer creation error: ${err.message}`);
    }
  };
  

  // const createOffer = async () => {
  //   try {
  //     if (!connection || connection.state !== "Connected") {
  //       throw new Error("Connection not ready");
  //     }

  //     if (!peerConnection.current) {
  //       await initializePeerConnection();
  //     }

  //     const offer = await peerConnection.current.createOffer({
  //       offerToReceiveAudio: true,
  //       offerToReceiveVideo: true
  //     });

  //     await peerConnection.current.setLocalDescription(offer);
  //     await connection.invoke("SendSignal", roomId, offer);
  //   } catch (err) {
  //     console.error('Offer creation error:', err);
  //     setError(`Offer creation error: ${err.message}`);
  //   }
  // };

  // const initializePeerConnection = async () => {
  //   try {
  //     if (peerConnection.current) {
  //       peerConnection.current.close();
  //     }

  //     peerConnection.current = new RTCPeerConnection(iceServers);

  //     if (localStream) {
  //       localStream.getTracks().forEach((track) => {
  //         peerConnection.current.addTrack(track, localStream);
  //       });
  //     }

  //     peerConnection.current.ontrack = (event) => {
  //       setRemoteStream(event.streams[0]);
  //       if (remoteVideoRef.current) {
  //         remoteVideoRef.current.srcObject = event.streams[0];
  //       }
  //     };

  //     peerConnection.current.onicecandidate = (event) => {
  //       if (event.candidate && connection && roomId) {
  //         connection.invoke("SendSignal", roomId, event.candidate)
  //           .catch(err => {
  //             console.error('Error sending ICE candidate:', err);
  //             setError(`Error sending ICE candidate: ${err.message}`);
  //           });
  //       }
  //     };

  //     peerConnection.current.oniceconnectionstatechange = () => {
  //       console.log("ICE Connection State:", peerConnection.current.iceConnectionState);
  //       if (peerConnection.current?.iceConnectionState === "failed") {
  //         peerConnection.current.restartIce();
  //       }
  //     };
  //   } catch (err) {
  //     console.error('Error initializing peer connection:', err);
  //     setError(`Error initializing peer connection: ${err.message}`);
  //   }
  // };

  const initializePeerConnection = async () => {
    try {
      console.log('Initializing peer connection...');
      
      if (peerConnection.current) {
        console.log('Closing existing peer connection...');
        peerConnection.current.close();
      }
  
      peerConnection.current = new RTCPeerConnection(iceServers);
      console.log('New peer connection created.');
  
      if (localStream) {
        console.log('Adding local stream tracks to peer connection...');
        localStream.getTracks().forEach((track) => {
          peerConnection.current.addTrack(track, localStream);
        });
      } else {
        console.error('Local stream is not available.');
      }
  
      peerConnection.current.ontrack = (event) => {
        console.log('Track received:', event.streams[0]);
        setRemoteStream(event.streams[0]);
        if (remoteVideoRef.current) {
          remoteVideoRef.current.srcObject = event.streams[0];
        }
      };
  
      peerConnection.current.onicecandidate = (event) => {
        if (event.candidate) {
          console.log('Sending ICE candidate...');
          if (connection && roomId) {
            connection.invoke("SendSignal", roomId, event.candidate)
              .catch(err => {
                console.error('Error sending ICE candidate:', err);
                setError(`Error sending ICE candidate: ${err.message}`);
              });
          }
        }
      };
  
      peerConnection.current.oniceconnectionstatechange = () => {
        console.log("ICE Connection State:", peerConnection.current.iceConnectionState);
        if (peerConnection.current?.iceConnectionState === "failed") {
          console.log('ICE connection failed, restarting...');
          peerConnection.current.restartIce();
        }
      };
    } catch (err) {
      console.error('Error initializing peer connection:', err);
      setError(`Error initializing peer connection: ${err.message}`);
    }
  };
  

  const handleReceiveOffer = async (offer) => {
    if (!connection || connection.state !== "Connected") {
      setError("SignalR connection not established");
      return;
    }

    try {
      await peerConnection.current.setRemoteDescription(new RTCSessionDescription(offer));
      const answer = await peerConnection.current.createAnswer();
      await peerConnection.current.setLocalDescription(answer);
      await connection.invoke("SendSignal", roomId, answer);
    } catch (err) {
      console.error('Error handling offer:', err);
      setError(`Error handling offer: ${err.message}`);
    }
  };

  const initLocalStream = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({
        video: true,
        audio: true
      });
      setLocalStream(stream);
      if (localVideoRef.current) {
        localVideoRef.current.srcObject = stream;
      }
    } catch (err) {
      console.error('Error accessing media devices:', err);
      setError(`Error accessing media devices: ${err.message}`);
    }
  };

  const joinRoom = async () => {
    if (!roomId.trim()) {
      setError("Please enter a room ID");
      return;
    }
    if (!connection) {
      setError("Connection not established");
      return;
    }
    try {
      await connection.invoke("JoinRoom", roomId);
      console.log("Joined room:", roomId);
    } catch (err) {
      console.error('Error joining room:', err);
      setError(`Error joining room: ${err.message}`);
    }
  };

  const leaveRoom = async () => {
    if (connection && roomId) {
      try {
        await connection.invoke("LeaveRoom", roomId);
        cleanupVideoCall();
        setRoomId("");
      } catch (err) {
        console.error('Error leaving room:', err);
        setError(`Error leaving room: ${err.message}`);
      }
    }
  };

  const cleanupVideoCall = () => {
    if (peerConnection.current) {
      peerConnection.current.close();
      peerConnection.current = null;
    }
    if (remoteVideoRef.current) {
      remoteVideoRef.current.srcObject = null;
    }
    setRemoteStream(null);
  };

  return (
    <div className="p-4">
      <h1 className="text-2xl font-bold mb-4">Video Call</h1>

      <div className="mb-4">
        <span className={`inline-block px-2 py-1 rounded ${connectionStatus === "Connected" ? "bg-green-500" :
          connectionStatus === "Connecting..." ? "bg-yellow-500" : "bg-red-500"
          } text-white mr-2`}>
          {connectionStatus}
        </span>
      </div>

      {error && (
        <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-4">
          {error}
        </div>
      )}

      <div className="mb-4">
        <input
          type="text"
          value={roomId}
          onChange={(e) => setRoomId(e.target.value)}
          placeholder="Enter Room ID"
          className="border p-2 mr-2 rounded"
          disabled={!isConnected}
        />
        <button
          onClick={joinRoom}
          disabled={!isConnected || !roomId.trim()}
          className="bg-blue-500 text-white px-4 py-2 rounded mr-2 disabled:bg-gray-400"
        >
          Join Room
        </button>
        <button
          onClick={leaveRoom}
          disabled={!isConnected || !roomId.trim()}
          className="bg-red-500 text-white px-4 py-2 rounded disabled:bg-gray-400"
        >
          Leave Room
        </button>
      </div>

      <div className="grid grid-cols-2 gap-4">
        <div>
          <h2 className="text-lg font-semibold mb-2">Local Video</h2>
          <video
            ref={localVideoRef}
            autoPlay
            muted
            playsInline
            className="w-full bg-black rounded"
          />
        </div>
        <div>
          <h2 className="text-lg font-semibold mb-2">Remote Video</h2>
          <video
            ref={remoteVideoRef}
            autoPlay
            playsInline
            className="w-full bg-black rounded"
          />
        </div>
      </div>
    </div>
  );
};

export default App;
